				var i, j, higher, swap;
				for (i = 0; i < mechanisms.length - 1; ++i) {
					higher = i;
					for (j = i + 1; j < mechanisms.length; ++j) {
						if (mechanisms[j].prototype.priority > mechanisms[higher].prototype.priority) {
							higher = j;
						}
					}
					if (higher != i) {
						swap = mechanisms[i];
						mechanisms[i] = mechanisms[higher];
						mechanisms[higher] = swap;
					}
				}
				return mechanisms;
